import React, { useRef, useEffect, useState, useCallback } from 'react';
import jsQR from 'jsqr';

function BarcodeScanner({ onDetected, onCancel }) {
  const videoRef = useRef(null);
  const canvasRef = useRef(null);
  const debugCanvasRef = useRef(null);
  const [error, setError] = useState(null);
  const [isScanning, setIsScanning] = useState(false);
  const [debugMode, setDebugMode] = useState(false);
  const [scanAttempts, setScanAttempts] = useState(0);
  const scanningRef = useRef(false);

  // Funkcja do skanowania kodw kreskowych z ulepszon detekcj
  const scanForBarcode = useCallback(() => {
    if (!scanningRef.current || !videoRef.current || !canvasRef.current) {
      return;
    }

    const video = videoRef.current;
    const canvas = canvasRef.current;
    const context = canvas.getContext('2d');

    if (video.readyState === video.HAVE_ENOUGH_DATA) {
      const { videoWidth, videoHeight } = video;
      
      if (videoWidth === 0 || videoHeight === 0) {
        console.log('Wideo nie ma jeszcze wymiarw, czekam...');
        requestAnimationFrame(scanForBarcode);
        return;
      }

      canvas.width = videoWidth;
      canvas.height = videoHeight;
      
      // Narysuj obecn klatk
      context.drawImage(video, 0, 0, videoWidth, videoHeight);
      
      // Debug: poka co widzi kamera
      if (debugMode && debugCanvasRef.current) {
        const debugCanvas = debugCanvasRef.current;
        const debugContext = debugCanvas.getContext('2d');
        debugCanvas.width = videoWidth / 4; // Mniejszy rozmiar dla debug
        debugCanvas.height = videoHeight / 4;
        debugContext.drawImage(video, 0, 0, debugCanvas.width, debugCanvas.height);
      }

      // Zwiksz licznik prb
      setScanAttempts(prev => {
        const newCount = prev + 1;
        
        // Loguj co pewien czas prby skanowania
        if (newCount % 60 === 0) { // Co ~2 sekundy przy 30 FPS
          console.log(` Skanowanie aktywne... prba ${newCount}. Rozdzielczo: ${videoWidth}x${videoHeight}`);
        }
        
        return newCount;
      });

      try {
        // Pobierz dane obrazu z rnych regionw
        const regions = [
          // Cay obraz
          { x: 0, y: 0, width: videoWidth, height: videoHeight },
          // rodkowa cz (tam gdzie jest ramka)
          { 
            x: Math.floor(videoWidth * 0.2), 
            y: Math.floor(videoHeight * 0.3), 
            width: Math.floor(videoWidth * 0.6), 
            height: Math.floor(videoHeight * 0.4) 
          },
          // Jeszcze mniejszy rodek
          { 
            x: Math.floor(videoWidth * 0.3), 
            y: Math.floor(videoHeight * 0.4), 
            width: Math.floor(videoWidth * 0.4), 
            height: Math.floor(videoHeight * 0.2) 
          }
        ];

        for (const region of regions) {
          const imageData = context.getImageData(region.x, region.y, region.width, region.height);
          
          // Sprbuj jsQR z rnymi opcjami
          const qrOptions = [
            { inversionAttempts: "dontInvert" },
            { inversionAttempts: "onlyInvert" },
            { inversionAttempts: "attemptBoth" }
          ];

          for (const options of qrOptions) {
            const code = jsQR(imageData.data, imageData.width, imageData.height, options);
            
            if (code && code.data) {
              console.log(' Znaleziono kod:', code.data);
              
              // Sprawd rne formaty kodw produktowych
              const cleanCode = code.data.replace(/\D/g, '');
              const originalCode = code.data.trim();
              
              // Akceptuj rne formaty
              const isValidBarcode = (
                // Standardowe kody kreskowe (8-14 cyfr)
                (cleanCode.length >= 8 && cleanCode.length <= 14) ||
                // Kody z prefiksami
                originalCode.match(/^(EAN|UPC|ISBN)?\s*\d{8,14}$/i) ||
                // Kody mieszane (cyfry i litery)
                originalCode.match(/^[A-Z0-9]{8,20}$/i)
              );

              if (isValidBarcode) {
                console.log(' Kod zaakceptowany:', cleanCode || originalCode);
                scanningRef.current = false;
                setIsScanning(false);
                onDetected(cleanCode || originalCode);
                return;
              } else {
                console.log(' Kod odrzucony (format):', originalCode, 'Dugo czystych cyfr:', cleanCode.length);
              }
            }
          }
        }

      } catch (scanError) {
        console.error('Bd podczas skanowania:', scanError);
      }
    } else {
      console.log('Wideo nie jest gotowe, readyState:', video.readyState);
    }

    // Kontynuuj skanowanie
    requestAnimationFrame(scanForBarcode);
  }, [debugMode, onDetected]);

  useEffect(() => {
    let videoStream = null;

    const startCamera = async () => {
      try {
        // Sprbuj rnych konfiguracji kamery
        const constraints = [
          // Konfiguracja 1: Tylna kamera z wysok rozdzielczoci
          {
            video: { 
              facingMode: 'environment',
              width: { ideal: 1920, min: 640 },
              height: { ideal: 1080, min: 480 },
              focusMode: 'continuous'
            }
          },
          // Konfiguracja 2: Tylna kamera z nisz rozdzielczoci
          {
            video: { 
              facingMode: 'environment',
              width: { ideal: 1280, min: 640 },
              height: { ideal: 720, min: 480 }
            }
          },
          // Konfiguracja 3: Dowolna kamera
          {
            video: { 
              width: { ideal: 1280, min: 640 },
              height: { ideal: 720, min: 480 }
            }
          },
          // Konfiguracja 4: Podstawowa
          {
            video: true
          }
        ];

        let stream = null;
        let lastError = null;

        for (const constraint of constraints) {
          try {
            console.log('Prbuj konfiguracj kamery:', constraint);
            stream = await navigator.mediaDevices.getUserMedia(constraint);
            console.log('Sukces z konfiguracj:', constraint);
            break;
          } catch (err) {
            console.log('Niepowodzenie z konfiguracj:', constraint, err);
            lastError = err;
          }
        }

        if (!stream) {
          throw lastError || new Error('Nie mona uruchomi adnej konfiguracji kamery');
        }
        
        videoStream = stream;
        
        if (videoRef.current) {
          videoRef.current.srcObject = stream;
          
          videoRef.current.onloadedmetadata = () => {
            console.log('Metadane wideo zaadowane');
            console.log('Rozdzielczo wideo:', videoRef.current.videoWidth, 'x', videoRef.current.videoHeight);
            
            videoRef.current.play().then(() => {
              console.log('Wideo rozpoczte, uruchamiam skanowanie...');
              setIsScanning(true);
              scanningRef.current = true;
              setTimeout(() => {
                scanForBarcode();
              }, 1000); // Dusze opnienie
            }).catch(err => {
              console.error('Bd odtwarzania wideo:', err);
              setError('Nie mona uruchomi podgldu kamery');
            });
          };
        }
      } catch (err) {
        console.error('Bd dostpu do kamery:', err);
        let errorMessage = 'Nie mona uzyska dostpu do kamery.';
        
        if (err.name === 'NotAllowedError') {
          errorMessage = 'Dostp do kamery zosta odrzucony. Kliknij ikon kamery w pasku adresu i zezwl na dostp.';
        } else if (err.name === 'NotFoundError') {
          errorMessage = 'Nie znaleziono kamery w urzdzeniu.';
        } else if (err.name === 'NotSupportedError') {
          errorMessage = 'Kamera nie jest obsugiwana przez przegldark.';
        }
        
        setError(errorMessage);
      }
    };

    startCamera();

    return () => {
      console.log(' Czyszczenie skanera...');
      scanningRef.current = false;
      setIsScanning(false);
      
      if (videoStream) {
        videoStream.getTracks().forEach(track => {
          track.stop();
        });
      }
    };
  }, [scanForBarcode]);

  const handleManualInput = () => {
    const barcode = prompt('Wprowad kod kreskowy rcznie:');
    if (barcode && barcode.trim()) {
      onDetected(barcode.trim());
    }
  };

  const toggleDebug = () => {
    setDebugMode(!debugMode);
  };

  if (error) {
    return (
      <div className="scanner-container">
        <div className="scanner-header">
          <h3> Bd kamery</h3>
          <button onClick={onCancel} className="cancel-button"></button>
        </div>
        <div className="scanner-error">
          <p><strong>{error}</strong></p>
          <div style={{ margin: '1rem 0' }}>
            <p><strong>Rozwizania:</strong></p>
            <ul style={{ textAlign: 'left', marginTop: '0.5rem' }}>
              <li>Kliknij ikon  lub  w pasku adresu</li>
              <li>Wybierz "Zezwl" na dostp do kamery</li>
              <li>Odwie stron (F5)</li>
              <li>Sprawd czy inne aplikacje nie blokuj kamery</li>
              <li>Uyj przegldarki Chrome lub Firefox</li>
            </ul>
          </div>
          <button onClick={handleManualInput} className="button-primary">
             Wprowad kod rcznie
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="scanner-container">
      <div className="scanner-header">
        <h3>
          {isScanning ? ` Skanowanie... (${scanAttempts})` : ' Uruchamianie...'}
        </h3>
        <div>
          <button onClick={toggleDebug} className="cancel-button" style={{ marginRight: '10px' }}>
            
          </button>
          <button onClick={onCancel} className="cancel-button"></button>
        </div>
      </div>
      
      <div className="scanner-viewport">
        <video 
          ref={videoRef}
          className="scanner-element"
          playsInline
          muted
          autoPlay
          style={{
            width: '100%',
            height: '100%',
            objectFit: 'cover'
          }}
        />
        <canvas ref={canvasRef} style={{ display: 'none' }} />
        
        <div className="scanner-overlay">
          <div className="scanner-frame"></div>
          <p className="scanner-instruction">
            {isScanning 
              ? ' Skieruj na kod kreskowy' 
              : ' adowanie...'}
          </p>
          {isScanning && (
            <div style={{ textAlign: 'center', marginTop: '10px' }}>
              <p style={{ fontSize: '0.8rem', color: 'white', background: 'rgba(0,0,0,0.5)', padding: '5px 10px', borderRadius: '10px' }}>
                 Ustaw kod w rodku ramki<br/>
                 Trzymaj stabilnie<br/>
                 Sprawd owietlenie
              </p>
            </div>
          )}
        </div>

        {debugMode && (
          <div style={{ 
            position: 'absolute', 
            top: '10px', 
            left: '10px', 
            background: 'rgba(0,0,0,0.8)', 
            padding: '10px',
            borderRadius: '5px',
            color: 'white',
            fontSize: '0.8rem'
          }}>
            <p> Tryb debug aktywny</p>
            <p>Prby: {scanAttempts}</p>
            <canvas 
              ref={debugCanvasRef}
              style={{ 
                border: '1px solid white', 
                maxWidth: '100px',
                display: 'block',
                marginTop: '5px'
              }}
            />
          </div>
        )}
      </div>
      
      <div className="manual-input">
        <button onClick={handleManualInput} className="button-primary">
           Wprowad kod rcznie
        </button>
        <div style={{ marginTop: '10px', fontSize: '0.8rem', color: '#666' }}>
          <p> <strong>Wskazwki:</strong></p>
          <p> Ustaw telefon 15-20cm od kodu</p>
          <p> Sprawd czy kod jest ostry i dobrze owietlony</p>
          <p> Sprbuj rnych ktw</p>
          <p> Wcz tryb debug () aby zobaczy co widzi kamera</p>
        </div>
      </div>
    </div>
  );
}

export default BarcodeScanner;
